import React, {Component} from "react";
import PropTypes from "prop-types";
import _ from "lodash";

class MultiCartExpandingSectionComponent extends Component {

    constructor(props) {
        super(props);

    }

    render() {
        return (

                <div role="tablist" aria-multiselectable="false" id={'bundle-' + this.props.entry.bundleNo}>
                    <div className="opl-checkout__section opl-checkout__section--togglable u-padding is-expanded">
                      <div className="opl-checkout__section__header">
                          <div className="l-full-row u-padding-top-l">
                              <div className="l-page-row u-small-padding-left">
                                <div className="u-padding-s u-small-padding u-padding-right-l u-small-no-padding-r">
                                  <table className="u-table-fixed">
                                    <thead>
                                      <tr>
                                        <th className="u-vertical-bottom u-text-left">
                                          <table className="u-small-w-100 u-w-auto">
                                            <tbody>
                                              <tr>
                                                <td className="u-vertical-bottom u-small-vertical-baseline u-small-w-100" style={{paddingBottom: 1}}>
                                                    <h3 className="h2 u-inline">

                                                            { this.props.title + " " + _.capitalize(this.props.entry.promotionType) }

                                                    </h3>
                                                </td>
                                                <td className="u-vertical-bottom u-padding-s u-padding-left-l">

                                                </td>
                                                <td className="u-vertical-bottom u-small-vertical-baseline u-padding-s u-padding-left-l u-small-no-padding-b">
                                                    <a href="#" onClick={ this.props.onRemoveClicked }>
                                                        {this.props.conf.deleteEntryLabelHeader}
                                                            <span className="u-acc-hide">{this.props.title}</span>
                                                    </a>
                                                </td>
                                            </tr></tbody>
                                          </table>
                                        </th>
                                        <th aria-hidden="true" className="u-padding-s u-padding-right-s l-col-2 u-vertical-top u-text-right u-small-hide">Opłaty <br className="u-small-hide u-large-hide"/>jednorazowe</th>
                                        <th aria-hidden="true" className="u-padding-s l-col-2 u-small-hide u-text-right u-vertical-top">Opłaty <br className="u-small-hide u-large-hide"/>miesięczne {!this.props.isB2B && (<span><br/>z rabatami</span>)}</th>
                                      </tr>
                                    </thead>
                                  </table>
                                </div>
                              </div>
                          </div>
                      </div>
                      <div aria-hidden="false" style={{display: 'block'}} className={'opl-checkout__section__content osc-' + this.props.entry.bundleNo} role="tabpanel">
                        { this.props.children[0] }
                      </div>
                      {
                           this.props.children[1]
                      }
                    </div>
                </div>

        )
    }
}

const EntryPropType = PropTypes.shape({
    bundleNo: PropTypes.number
});

const ConfPropType = PropTypes.shape({
    deleteEntryLabelHeader: PropTypes.string
});

MultiCartExpandingSectionComponent.propTypes = {
    title: PropTypes.string,
    trigger: PropTypes.string.isRequired,
    entry: EntryPropType.isRequired,
    conf: ConfPropType.isRequired,
    children: PropTypes.array.isRequired,
    onRemoveClicked: PropTypes.func,
    isB2B: PropTypes.bool
};

export default MultiCartExpandingSectionComponent;